body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {margin: 0; height: 100%; overflow: hidden}
#notes{
    margin: 0px;
    padding: 0px;
    margin-bottom: 0;
    font-family: 'Arial Rounded MT Bold', sans-serif;
}

#boilerplateHeader{
    font-size: 20px;
    margin-left: 10px;
}

#boilerplateBody{
    margin-left: 5px;
}



p{
    margin-bottom: 0;
}
.App{
    font-family: 'Arial Rounded MT Bold', sans-serif;
}

#buttons{
    position: absolute;
    top: 80%;
    left: 15%;
}

#description{
    padding-bottom: 0px;
    margin-bottom: 0px;
    margin-top: 0px;
    font-size: 15px;
}

#pomodoroText{
    color: red;
}

#breakText{
    color:rgb(245, 185, 57);
}

#TwentyFiveMinButton {
    margin-right: 10px;
}

#FiveMinButton {
    margin-right: 10px;
}

#FifteenMinButton {
    margin-right: 10px;
}

#StartButton {
    margin-right: 10px;
}

#pomodoro {
  top: 0%;
  text-align: center;
  position: fixed;
  left:50%;
  height:50%;
  width:50%;
  border-width:  10px;
  border-style: double;
  border-color: rgb(77, 147, 252);
}

#tasks {
  position: fixed;
  height:50%;
  width:50%;
  left: 0;
  border-width: 10px;
  border-style: double;
  border-color: rgb(77, 147, 252);
}

#notes {
  position: fixed;
  top: 50%;
  left: 0;
  right: 0;
  height: 50%;
  border-width: 10px;
  border-style: double;
  border-color: rgb(77, 147, 252);
}

#timer{
    position: fixed;
    bottom: 51%;
    left: 15px;
}

#taskTitle{
    font-size: 30px;
    margin-left: 10px;
}

#taskForm{
    position: fixed;
    right: 51%;
    width: 190px;
    top: 13px;
}
#taskBullet{
    margin-left:18px;
    font-size: 20px;
}

#taskButtons{
    position: fixed;
    height: 42.5%;
    right: 40%;
}

#taskButton{
    background-color: rgba(52, 52, 52, 0.0);
    border-color: rgba(52, 52, 52, 0.0);
}

#moveButton{
    background-color: rgba(52, 52, 52, 0.0);
    border-color: rgba(52, 52, 52, 0.0);
}
